  delete = async (item, index) => {
    let sendData = {
      id: item.id,
    };
    let result = await HttpClient.requestData("delete", "POST", sendData);
    if (result && result.status) {
      let index = this.state.result.indexOf(item);
      if (index > -1) {
        this.state.result.splice(index, 1);
        // this.setState({
        //   alert: true,
        //   message: "Deleted Succfully",
        //   type: "success",
        // });
        this.setState({
          type: "success",
          status: true,
          title: "Deleted Succfully",
        });
        // setTimeout(() => {
        //   this.setState({
        //     alert: false,
        //     message: "",
        //     type: "",
        //   });
        // }, 3000);
        this.fetch();
      }
    }
  };

  blankChk = (index) => {
    if (
      this.state.question[index].answer != "" &&
      this.state.question[index].question != "" &&
      this.state.question[index].option1 != "" &&
      this.state.question[index].option2 != "" &&
      this.state.question[index].option3 != "" &&
      this.state.question[index].option4 != ""
    ) {
      return true;
    } else {
      return false;
    }
  };

  optionChk = (index) => {
    if (
      this.state.question[index].answer == this.state.question[index].option1 ||
      this.state.question[index].answer == this.state.question[index].option2 ||
      this.state.question[index].answer == this.state.question[index].option3 ||
      this.state.question[index].answer == this.state.question[index].option4
    ) {
      return true;
    } else {
      return false;
    }
  };

  formValid = async () => {
    if (
      this.state.quizName != "" &&
      this.state.startDate != "" &&
      this.state.endDate != ""
    ) {
      const startDate = moment(this.state.startDate);
      const timeEnd = moment(this.state.endDate);
      const diff = timeEnd.diff(startDate);
      const diffDuration = moment.duration(diff);
      if (diffDuration.days() > 0) {
        let blank = this.blankChk(0);
        console.log("blank", blank);
        if (blank) {
          let option = this.optionChk(0);
          console.log("option", option);

          if (option) {
            // this.tog_CardModal();
          } else {
            alert("Answer Must Be In Options");
          }
        } else {
          alert("Please Add Atleast One Question.");
        }
      } else {
        alert("Invalid End Date Details.");
      }
    } else {
      alert("Please Fillup Details. ll");
    }
  };

  handlesubmit = async () => {
    if (
      this.state.question == "" &&
      this.state.option1 == "" &&
      this.state.option2 == "" &&
      this.state.option3 == "" &&
      this.state.option4 == ""
    ) {
      // alert("Please Fillup Details");

      this.setState({
        type: "error",
        status: true,
        title: "Please Fillup Details",
      });
    } else {
      let data = {
        question: this.state.question,
        option1: this.state.option1,
        option2: this.state.option2,
        option3: this.state.option3,
        option4: this.state.option4,
        priceid: parseInt(this.state.selectedPlan),
      };

      console.log("DATA", data);
      let result = await HttpClient.requestData("addquestion", "POST", data);

      console.log("1stpagesubmit", result);

      if (result && result.status) {
        this.setState({
          // alert: true,
          // message: "Successfully Submited.",
          // type: "success",
          type: "success",
          status: true,
          title: "Successfully Submited.",
        });
        this.fetch();
        this.setState({
          question: "",
          option1: "",
          option2: "",
          option3: "",
          option4: "",
        });
      } else {
        this.setState({
          type: "error",
          status: true,
          title: result.error,
        });
      }
      // setTimeout(() => {
      //   this.setState({
      //     alert: false,
      //     message: "",
      //     type: "",
      //   });
      // }, 3000);
    }
  };
  submitFrom = async () => {
    let data = {
      data: this.state.question,
      owner_id: this.state.userId,
      type: "Quiz",
      quizName: this.state.quizName,
      image: this.state.imgUrl,
      startDate: this.state.startDate,
      endDate: this.state.endDate,
    };
    let result = await HttpClient.requestData("admin/quiz", "POST", data);
    if (result && result.status) {
      this.setState({
        alert: true,
        message: "Successfully Submited.",
        type: "success",
      });
      this.fetch();
      this.setState({
        question: [
          {
            question: "",
            option1: "",
            option2: "",
            option3: "",
            option4: "",
            answer: "",
          },
        ],
      });
    } else {
      alert(result.error);
    }
    setTimeout(() => {
      this.setState({
        alert: false,
        message: "",
        type: "",
      });
    }, 3000);
  };

